*,
::after,
::before {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

html {
    font-size: 100%;
}

:root {
  /* colors  */
  --primary-100: #e6f0ff;
  --primary-200: #b4d3fe;
  --primary-300: #82b6fd;
  --primary-400: #5199fc;
  --primary-500: #207cfc;
  --primary-600: #1a64c2;
  --primary-700: #144a88;
  --primary-800: #0e3150;
  --primary-900: #071716;

  /* grey  */
  --grey-50: #f8fafc;
  --grey-100: #f1f5f9;
  --grey-200: #e2e8f0;
  --grey-300: #cbd5e1;
  --grey-400: #94a3b8;
  --grey-500: #64748b;
  --grey-600: #475569;
  --grey-700: #334155;
  --grey-800: #1e293b;
  --grey-900: #0f172a;

  /* rest of the colors  */
  --black: #222;
  --white: #fff;
  --red-light: #f8d7da;
  --red-dark: #842029;
  --green-light: #d1e7dd;
  --green-dark: #0f5132;

  /* fonts  */
  --small-text: 0.875rem;;
  --extra-small-text: 0.7rem;

  /* rest of the vars  */
  --backgroundColor: var(var(--grey-100));
  --textColor: var(--grey-900);
  --borderRadius: 0.25rem;
  --letterSpacing: 1px;
  --transition: all 0.3s ease-in-out; /* possible to change  */
  --max-width: 1120px;
  --fixed-width: 600px;
  --view-width: 90vw;

  /* box shadow  */
  --shadow-1: 0 1px 3px 0 rgba(0, 0, 0, 0.1),
    0 1px 2px 0 rgba(0, 0, 0, 0.06);
  --shadow-2: 0 4px 6px -1px rgba(0, 0, 0, 0.1),
    0 2px 4px -1px rgba(0, 0, 0, 0.06);
  --shadow-3: 0 10px 15px -3px rgba(0, 0, 0, 0.1),
    0 4px 6px -2px rgba(0, 0, 0, 0.05);
  --shadow-4: 0 20px 25px -5px rgba(0, 0, 0, 0.1),
    0 10px 10px -5px rgba(0, 0, 0, 0.04);
  
}

body {
    background: var(--backgroundColor);
    font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif, 'Apple Color Emoji', 'Segoe UI Emoji', 'Segoe UI Symbol';
    font-weight: 400;
    line-height: 1.75;
    color: var(--textColor);
}

p {
    margin-bottom: 1.5rem;
    max-width: 40rem;
}

h1,
h2,
h3,
h4,
h5 {
    margin: 0;
    margin-bottom: 1.38rem;
    font-weight: 400;
    line-height: 1.3;
    text-transform: capitalize;
    letter-spacing: var(--letterSpacing);

}

h1 {
    margin-top: 0;
    font-size: 3.052rem;
}

h2 {
    font-size: 2.441rem;
}

h3 {
    font-size: 1.953rem;
}

h4 {
    font-size: 1.563rem;
}

h5 {
    font-size: 1.25rem;
}

small,
.text-small {
    font-size: var(--small-text);
}

a 
{
    text-decoration: none;
    
}

ul {
    list-style-type: none;
    padding: 0;
}

.img {
    width: 100%;
    display: block;
    object-fit: cover;
}

/* buttons  */

.btn {
    cursor: pointer;
    color: var(--white);
    background: var(--primary-500);  
    border: transparent;
    border-radius: var(--borderRadius);
    letter-spacing: var(--letterSpacing);
    padding: 0.375rem 0.75rem;
    box-shadow: var(--shadow-1);
    display: inline-block;
   
    text-transform: capitalize;
    
    transition: var(--transition);
 
}

.btn:hover {
    background: var(--primary-700);
    box-shadow: var(--shadow-3);
}

.btn-hipster {
    background: var(--primary-200);
    color: var(--primary-500);
    
}

.btn-hipster:hover {
    background: var(--black);
    color: var(--white);
}

.btn-block {
    
    width: 100%;
}


/* end of global style  */

/* SEARCH */
.search-container {
  height: 5rem;
  background: var(--white);
  display: flex;
  justify-content: center;
  align-items: center;
  
}

.search-container form {
  width : var(--view-width);
  max-width: var(--max-width);
  display: flex;
  gap: 0.5rem;
  flex-wrap: wrap;
  
}

.search-container form input {
  max-width: 200px;
  background: var(--backgroundColor);
  padding: 0.375rem 0.75rem;
  border-radius: var(--borderRadius);
  border: 1px solid var(--grey-300);
  
  font-size: 1rem;  /* can be changed later */
  letter-spacing: var(--letterSpacing);
}

.search-container form input:hover {
  border : 1px solid var(--primary-400);
  

}

::placeholder {
  font-family: inherit;
  color: var(--grey-400);
  text-transform: capitalize;
}

.search-container btn {
  font-size: 1.75rem;
  
}

/* FAVORITES */

.favorites {
  background: var(--black);
  color: var(--white);
  padding: 1rem 0;
  
}

.favorites-content {
  width: var(--view-width);
  max-width: var(--max-width);
  margin: 0 auto;
}

.favorites-container  {
  display:flex;
  gap:0.5rem;
  flex-wrap: wrap;
}

.favorite-item {
  text-align: center;
}

.favorites-img {
  width : 60px;
  border-radius: 50%;
  border: 5px solid var(--white);
  cursor: pointer;
}

.remove-btn {
  margin-top: 0.25rem;
  background: transparent;
  border: transparent;
  color: var(--white);
  cursor: pointer;
  font-size: 0.75rem;
  transition: var(--transition);
}

.remove-btn:hover {
  color: var(--red-dark);
  
}


/* MEALS */

.section,.section-center {
  padding: 3rem 0;
  width : var(--view-width);
  max-width: var(--max-width);
  margin: 0 auto;

}

.section-center {
  
  display:grid;
  gap: 2rem ;
}

@media screen and (min-width: 776px) {
  .section-center {
    grid-template-columns: 1fr 1fr;
  }
}

@media screen and (min-width: 992px) {
  .section-center {
    grid-template-columns: 1fr 1fr 1fr;
  }
}

.single-meal {
  background: var(--white);
  color : var(--textColor);
  border-radius: var(--borderRadius);
  box-shadow: var(--shadow-2);
  transition: var(--transition);
}

.single-meal:hover {
  box-shadow: var(--shadow-4);
}

.single-meal img {
 
  height: 15rem;
  
  border-top-left-radius: var(--borderRadius);
  border-top-right-radius: var(--borderRadius);
  cursor:pointer;
}

.single-meal h5 {
  padding: 0.5rem 1rem;
  margin : 0;
  
}

.single-meal footer {
  padding: 1rem 1.5rem;
  display:flex;
  align-items: center;
  justify-content: space-between;
  flex-wrap : wrap;
}

.like-btn {
  background: transparent;
  border: transparent;
  font-size: 1.5rem;
  
  cursor: pointer;
  
  transition: var(--transition);
}

.like-btn:hover {
  color: var(--red-dark);
  transform: translateY(-2px);
}


/* MODALS */

.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0,0,0,0.85);
  display: grid;
  place-items: center;
  transition: var(--transition);
  z-index: 100;
  
}

.modal-container {
  background: var(--white);
  height: 80vh;
  overflow: scroll;
  border-radius: var(--borderRadius);
  width: 80%vw;
  max-width: 800px;
  
}

.modal-img {
 
  height: 15rem;
  
  border-top-left-radius: var(--borderRadius);
  border-top-right-radius: var(--borderRadius);
}

.modal-content {
  padding: 1rem 1.5rem;

}

.modal-content p 
{
  color: var(--grey-600);
  
}

.modal-content a{
  display: block;
  color: var(--primary-500);
  margin-bottom: 1rem;
  text-decoration: underline;
  transition: var(--transition);
}

.modal-content a:hover {
  color: var(--black);
}

.close-btn {
  
  background: var(--red-dark);
  color: var(--white);
  
  

}